import React from "react";
import "./membersMobile.css";
import Profile from "../components/profile";
import Julie from "../images/Julie.jpg"
import Mark from "../images/Mark.jpg";
import Helen from "../images/Helen.png";
import Theresa from "../images/Theresa.JPG";

class members1 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="parentContainerMembersMobile" style={{height: (this.state.height-50)+'px' , postion:"relative", bottom:'0px'}}>
             <h1 className="pageTitleMobile">Board Members</h1>
       <Profile
          Name={"Julie Schilling"}
          Description={"Board Member"}
          Image={Julie}
          width={"50%"}
          height={"30%"}
        />
        <Profile
          Name={"Mark Crouchet"}
          Description={"Board Chairman"}
          Image={Mark}
          width={"50%"}
          height={"30%"}
        />
        <Profile
          Name={"Helen Canatsey"}
          Description={"Board Member"}
          Image={Helen}
          width={"50%"}
          height={"30%"}
        />
        <Profile
          Name={"Theresa Charvet"}
          Image={Theresa}
          Description={"Board Member"}
          width={"50%"}
          height={"30%"}
        />
      </div>
    );
  }
}
export default members1;
