import React from "react";
import "./membersMobile.css";
import Profile from "../components/profile";
import JohnDoe from "../images/profileIcon.png"
import Terry from "../images/Terry.JPG";
import Bill from "../images/Bill.png";

class members extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
        <div className="parentContainerMembersMobile" style={{height: (this.state.height-50)+'px' , postion:"relative", bottom:'0px'}}>
        <Profile
          Name={"Terry Osman"}
          Description={"Treasurer"}
          Image={Terry}
          width={"50%"}
          height={"30%"}
        />
        <Profile
          Name={"Bill Cox"}
          Description={"Board Member"}
          Image={Bill}
          width={"50%"}
          height={"30%"}
        />
        <Profile
          Name={"Kathy Cluck"}
          Description={"Secretary"}
          Image={JohnDoe}
          width={"50%"}
          height={"30%"}
        />
        <Profile
          Name={"John Doe"}
          Description={"Board Member"}
          Image={JohnDoe}
          width={"50%"}
          height={"30%"}
        />
      </div>
    );
  }
}
export default members;
